import { Database } from "./Database";
import { IDiscount } from "./interface/IDiscount";
import { IPreschool } from "./interface/IPreschool";
import { IUser } from "./interface/IUser";
import { DiscountType, OrganizationName } from "./enum/types";

/**
 * ndirim hesaplamalarnn yapld snftr.
 */
export class DiscountCalculator {
  /**
   * Kullanc ve Anaokulu nesneleri alarak indirim hesaplamasn, indirim tipi olan yzde ve miktar cinsinden hesaplamasn yapan metod databse snfda tanml discount listesini kullanr.
   */
  static calculateDiscount(user: IUser, preschool: IPreschool) {
    
    let percent: number = 0;
    let amount: number = 0;
    Database.discounts.forEach(function(value, index, array) {
      if(value.DiscountType == "PERCENTAGE")
      {
         percent += DiscountCalculator.executeCalculateMethods(
            user,
            preschool,
            value
          );
      }
      switch (value.DiscountType) {
        case DiscountType.PERCENTAGE: {
          percent += DiscountCalculator.executeCalculateMethods(
            user,
            preschool,
            value
          );
          break;
        }
        case DiscountType.AMOUNT: {
          amount += DiscountCalculator.executeCalculateMethods(
            user,
            preschool,
            value
          );
          break;
        }
      }
    });
    return DiscountCalculator.calculatePriceWithDiscount(
      percent,
      amount,
      preschool
    );
  }
  /**
   * ndirim hesaplamas yapan 3 metod calculateEarlyRegistrationDiscount, calculateOrganizationDiscount, calculateUserTypeDiscount
   * ynlendirme yaparak toplam sonucu dner
   */
  static executeCalculateMethods(
    user: IUser,
    preschool: IPreschool,
    discount: IDiscount
  ) {
    let result = 0;

    result += Number(
      DiscountCalculator.calculateEarlyRegistrationDiscount(discount, preschool)
    );
    result += Number(
      DiscountCalculator.calculateOrganizationDiscount(
        discount,
        user,
        preschool
      )
    );
    result += Number(
      DiscountCalculator.calculateUserTypeDiscount(discount, user, preschool)
    );

    return result;
  }
  /**
   * Erken kayt indirimini hesaplar, erken kayt bilgisi kullancnn seti anaokulundan gelmektedir.
   */
  static calculateEarlyRegistrationDiscount(
    discount: IDiscount,
    preschool: IPreschool
  ) {
    let result = 0;
    debugger;
    if (
      DiscountCalculator.calculateEarlyRegistration(preschool) &&
      discount.DiscountName == "Erken Kayt Indirimi"
    ) {
      
      discount.DiscountValues.forEach((discountValue) => {
        if(discountValue.preschool.id == preschool.Id)
          result = Number(discountValue.value);
          });
    }
    return result;
  }
  /**
   * Kii tip indirimni hesaplar, kiiye zel indirim allan kurum seilmeden ve tek bir kii tipi seilmi olduunda hesaplamaktadr.
   */
  static calculateUserTypeDiscount(
    discount: IDiscount,
    user: IUser,
    preschool: IPreschool
  ) {
    let result = 0;
    if (
      discount.UserTypes.length == 1 &&
      discount.UserTypes.includes(user.TypeOfUser) &&
      discount.OrganizationName == OrganizationName.NONE
    ) {
      discount.DiscountValues.forEach((discountValue) => {
        if(discountValue.preschool.id == preschool.Id)
          result = Number(discountValue.value);
          });
    }
    return result;
  }
  /**
   * allan kurum indirimini hesaplar, Kurumun NONE olmamas, kullancnn girdii kurum ile indirimin kurumunun ayn olmas ve indirim listesinde kurumun bulunmas kontrollerini salar.
   */
  static calculateOrganizationDiscount(
    discount: IDiscount,
    user: IUser,
    preschool: IPreschool
  ) {
    let result = 0;
    if (
      discount.OrganizationName != OrganizationName.NONE &&
      discount.OrganizationName == user.OrganizationOfUser &&
      discount.UserTypes.includes(user.TypeOfUser)
    ) {
      discount.DiscountValues.forEach((discountValue) => {
        if(discountValue.preschool.id == preschool.Id)
          result = Number(discountValue.value);
          });
    }
    return result;
  }
  /**
   * Yzde ve miktar olarak hesaplanan indirimlerin anaokulu cretine uygulanm halini dnen metod
   */
  static calculatePriceWithDiscount(
    percent: number,
    amount: number,
    preschool: IPreschool
  ) {
    if (percent > 100) return 0;
    return Number(preschool.Price - (preschool.Price * percent) / 100 - amount);
  }

  /**
   * Anaokulunun erken kayt tarihini bugn ile kyaslayarak
   */
  static calculateEarlyRegistration(preschool: IPreschool) {
    let preschoolEarltRegistrationDate = new Date(
      preschool.EndOfEarlyRegistrationDate
    );
    let now = new Date();
    return preschoolEarltRegistrationDate > now;
  }
}
